___CSS_LOADER_EXPORT___.push([module.id, ".multiselect__tag{background:#d4af37!important}.multiselect__option--highlight,.multiselect__option--highlight:after{background:#d4af37}", "",{"version":3,"sources":["webpack://./resources/js/Pages/Admin/Admins/Show.vue"],"names":[],"mappings":"AA8LA,kBACE,4BACF,CAKA,sEACE,kBACF","sourcesContent":["<template>\n  <admin-layout>\n    <template #header>\n      <div class=\"flex justify-between items-center\">\n        <h2 class=\"font-semibold text-xl text-gray-700 leading-tight\">\n          Admins Role Update\n        </h2>\n      </div>\n    </template>\n\n    <div\n      class=\"flex flex-col mx-auto h-auto w-2/3 md:w-1/2 px-6 py-2 text-xs sm:text-sm\"\n    >\n      <div>\n        <h2\n          class=\"text-center text-lg md:text-xl font-semibold capitalize underline text-gray-700\"\n        >\n          Current Role: {{ admin.name }}\n        </h2>\n        <form @submit.prevent=\"submit\">\n          <div class=\"flex flex-col mt-2\">\n            <jet-label\n              for=\"name\"\n              value=\"Name\"\n              class=\"text-lg md:text-xl\"\n            ></jet-label>\n            <jet-input\n              id=\"name\"\n              type=\"text\"\n              class=\"mt-1 md:block w-full disabled:opacity-50\"\n              v-model=\"form.name\"\n              autofocus\n              disabled\n            ></jet-input>\n            <jet-input-error\n              :message=\"form.errors.name\"\n              class=\"mt-2\"\n            ></jet-input-error>\n          </div>\n\n          <div class=\"flex flex-col mt-2\">\n            <jet-label\n              for=\"email\"\n              value=\"E-mail\"\n              class=\"text-lg md:text-xl\"\n            ></jet-label>\n            <jet-input\n              id=\"email\"\n              type=\"email\"\n              class=\"mt-1 block w-full disabled:opacity-50\"\n              v-model=\"form.email\"\n              autofocus\n              disabled\n            ></jet-input>\n            <jet-input-error\n              :message=\"form.errors.email\"\n              class=\"mt-2\"\n            ></jet-input-error>\n          </div>\n\n          <div class=\"flex flex-col mt-2\">\n            <jet-label\n              for=\"created_at\"\n              value=\"Joined\"\n              class=\"text-lg md:text-xl\"\n            ></jet-label>\n            <jet-input\n              id=\"created_at\"\n              type=\"text\"\n              class=\"mt-1 block w-full disabled:opacity-50\"\n              v-model=\"form.created_at\"\n              autofocus\n              disabled\n            ></jet-input>\n            <jet-input-error\n              :message=\"form.errors.created_at\"\n              class=\"mt-2\"\n            ></jet-input-error>\n          </div>\n\n          <div class=\"flex flex-col mt-2\">\n            <div class=\"inline-block relative\">\n              <jet-label\n                for=\"role\"\n                value=\"Role\"\n                class=\"text-lg md:text-xl\"\n              ></jet-label>\n              <!-- <select\n                class=\"block capitalize border-yellow-300 focus:border-yellow-400 focus:ring focus:ring-yellow-200 focus:ring-opacity-50 rounded-md shadow-sm mt-1 w-full\"\n                v-model=\"form.roles[0][0].name\"\n                tabindex=\"1\"\n              >\n                <option\n                  v-for=\"(option, index) in allRoles\"\n                  :key=\"index\"\n                  :value=\"option.name\"\n                  :selected=\"option.name === form.roles[0][0].name\"\n                >\n                  {{ option.name }}\n                </option>\n              </select> -->\n              <multiselect\n                v-model=\"form.roles[0][0]\"\n                :options=\"allRoles\"\n                label=\"name\"\n                track-by=\"id\"\n                :tabindex=\"1\"\n                placeholder=\"Select a new role\"\n              ></multiselect>\n            </div>\n          </div>\n\n          <div class=\"flex items-center justify-center mt-12\">\n            <green-button\n              type=\"submit\"\n              class=\"text-sm ml-4\"\n              :class=\"{ 'opacity-25': form.processing }\"\n              :disabled=\"form.processing\"\n              >Update</green-button\n            >\n            <jet-danger-button\n              :href=\"route('AdminControlPanel.admins.index')\"\n              class=\"text-sm ml-4\"\n              >Cancel</jet-danger-button\n            >\n          </div>\n        </form>\n      </div>\n    </div>\n  </admin-layout>\n</template>\n\n<script>\nimport AdminLayout from \"@/Layouts/AdminLayout\";\nimport GreenButton from \"@/Components/GreenButton\";\nimport JetDangerButton from \"@/Jetstream/DangerButton\";\nimport JetInput from \"@/Jetstream/Input\";\nimport JetInputError from \"@/Jetstream/InputError\";\nimport JetLabel from \"@/Jetstream/Label\";\nimport Multiselect from \"@suadelabs/vue3-multiselect\";\n\nexport default {\n  props: [\"admin\", \"allRoles\"],\n  components: {\n    AdminLayout,\n    GreenButton,\n    JetDangerButton,\n    JetInput,\n    JetInputError,\n    JetLabel,\n    Multiselect,\n  },\n  data() {\n    return {\n      selected: null,\n      form: this.$inertia.form({\n        name: this.admin.name,\n        email: this.admin.email,\n        created_at: this.admin.created_at,\n        roles: [],\n      }),\n    };\n  },\n  created() {\n    if (this.admin.roles) {\n      return this.form.roles.push(this.admin.roles);\n    }\n  },\n  methods: {\n    submit() {\n      console.log(this.form.roles[0][0].name);\n      this.form.patch(\n        this.route(\"AdminControlPanel.admins.update\", this.admin.id),\n        {\n          preserveScroll: true,\n          onSuccess: () => {\n            Toast.fire({\n              icon: \"success\",\n              title: `You have successfully edited ${this.admin.name} role`,\n            }),\n              this.form;\n          },\n        }\n      );\n    },\n  },\n};\n</script>\n\n<style>\n.multiselect__tag {\n  background: #d4af37 !important;\n}\n.multiselect__option--highlight {\n  background: #d4af37;\n}\n\n.multiselect__option--highlight::after {\n  background: #d4af37;\n}\n</style>"],"sourceRoot":""}]);
